#login {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#login b {
  background: linear-gradient(90deg, #00c2cb, #d900ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#login h1 {
  color: white;
  font-family: titillium web, sans-serif;
  font-size: 5vw;
  margin-bottom: 10vh;
  margin-top: 30vh;
}

#login #login-fail {
  color: red;
  font-family: titillium web, sans-serif;
  font-size: 1.6vw;
  margin-top: 10vh;
}
