#scanner {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  color: white;
}

#scanner .camera {
  width: 100%;
}

#scanner p {
  text-align: center;
  font-size: 3vh;
}

#scanner a {
  text-decoration: underline;
  cursor: pointer;
  color: white;
}

#scanner span {
  margin-top: 10%;
  font-size: 3vh;
}

#scanner b {
  background: linear-gradient(45deg, #22ffff, #ff00dd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
